body {
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue",
    Helvetica, Arial, "Lucida Grande", sans-serif;
  font-size: 12px;
}

.fon {
  background-image: url(../../img/createHero/fon.jpg);
  background-size: cover;
  min-height: 100vh;
  display: grid;
  align-items: center;
  justify-items: center;
}

.authForm {
  background: #111;
  width: 300px;
  margin: 30px auto;
  border-radius: 0.4em;
  border: 1px solid #191919;
  overflow: hidden;
  position: relative;
  box-shadow: 0 5px 10px 5px rgba(0, 0, 0, 0.2);
}

.authForm:after {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  width: 100px;
  left: 20%;
  background: linear-gradient(45deg, #111, #444, #b6b6b8, #444, #111);
  top: 0;
}

.authForm:before {
  content: "";
  display: block;
  position: absolute;
  width: 8px;
  height: 5px;
  border-radius: 50%;
  left: 34%;
  top: -7px;
  box-shadow: 0 0 6px 4px #fff;
}

.inset {
  padding: 20px;
  border-top: 1px solid #19191a;
}

.authForm h1 {
  font-size: 18px;
  text-shadow: 0 1px 0 black;
  text-align: center;
  padding: 15px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 1);
  position: relative;
}

.label {
  color: #666;
  display: block;
  padding-bottom: 9px;
}

.input {
  color: white;
  width: 100%;
  padding: 8px 5px;
  background: linear-gradient(#1f2124, #27292c);
  border: 1px solid #222;
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.1);
  border-radius: 0.3em;
  margin-bottom: 20px;
}

.pContainer {
  padding: 0 20px 20px 20px;
}

.pContainer:after {
  clear: both;
  display: table;
  content: "";
}

.pContainer span {
  display: block;
  float: left;
  color: #0d93ff;
  padding-top: 8px;
}

.button {
  padding: 10px 20px;
  border: 1px solid rgba(0, 0, 0, 0.4);
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.4);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.3),
    inset 0 10px 10px rgba(255, 255, 255, 0.1);
  border-radius: 0.3em;
  background: #0184ff;
  color: white;
  float: right;
  font-weight: bold;
  cursor: pointer;
  font-size: 15px;
}

.button:hover {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.3),
    inset 0 -10px 10px rgba(255, 255, 255, 0.1);
}

.input[type="text"]:hover,
.input[type="password"]:hover,
.label:hover ~ .input[type="text"],
.label:hover ~ .input[type="password"] {
  background: #27292c;
}
